.App {
  text-align: center;
}

h1 {
  position: sticky;
  //margin-left: 50vw;
}

.vid-cont {
  position: fixed;
  left: 0;
  top : 0;
  text-align: left;
  width : 100%;
  height: 100%;
  &::before {
    font-size: small;
    margin: 10px;
    color: DarkGreen;
    content: attr(data-src);
  }
  z-index: -100;
}

.vid-fw {
  position: fixed;
  left: 0;
  top : 0;
  width : 100%;
  height: 100%;
  &:hover {
    box-shadow: 0 0 10px 6px Magenta;
  }
  &:focus {
    outline: 0;
  }
  border-radius: 5%;
  border: 2px solid Silver;
  box-shadow: 0 0 10px 6px Cyan;
  object-fit: cover;
  z-index: -100;
  filter: brightness(2.5);
  //pointer-events: none;
  transition: all 1s ease-in;
}

@mixin btnDemo($borderColor) {
  background-color: Gainsboro;
  border: 2px solid $borderColor;
  color: black;
  padding: 4px 4px;
  font: small "Courier New", sans-serif;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
}

.btn-cont {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-end;
  margin-right: 10px;
  padding-top: 120px;
}

.test-btn {
  left: 10px;
  top:  10px;
  padding: 4px;
  margin:  2px;
  border-radius: 10%;
  @include btnDemo(#4caf50);
}

.test-btn:disabled,
test-btn[disabled] {
  border: 1px solid olive;
  background-color: darkgray;
  color: SlateGray;
}
